<template>
  <v-container>
    <date-picker v-model="dates" ></date-picker>
    <v-text-field class="my-4" outlined v-model="search"
    prepend-inner-icon="mdi-magnify" hide-details 
    placeholder="Search Owner..."
    dense style="max-width: 50%"
    clearable></v-text-field>
    <v-divider class="my-6"></v-divider>
    <v-progress-linear indeterminate v-if="loading"></v-progress-linear>
    <v-tabs v-model="tab">
      <v-tab v-for="(tab, ind ) in tabs" :key="ind" >
        <v-icon class="mx-1">{{tab.icon}}</v-icon>{{tab.title}}
      </v-tab>
    </v-tabs>
    <v-layout d-flex  column >
       <v-flex cols="12" class="my-4"  >
       <data-table :items="total.items" :headers="total.headers" hide-default-footer></data-table>
      </v-flex>
      <v-flex cols="12"  >
       <data-table :items="table.items" :headers="table.headers"></data-table>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
// import utility from '../utility';
import DatePicker from './../components/dashboard/DatePicker' 
import debounce from 'debounce'
import DataTable from './../components/DataTable'
export default {
  components: {
    DatePicker,
    DataTable
  },
  computed: {
    active () {
      return this.tabs[this.tab]
    }
  },
  data: () => ({
    tab: 0,
    tabs: [
      {
        title: "FTD's",
        icon: "mdi-account-cash",
        api: {
          items: 'deposit/sales?type=ftd',
          total: 'deposit/sales?type=ftd&total=1',
        }
      },
      {
        title: "Retention's",
        icon: "mdi-cash-multiple",
        api: {
          items: 'deposit/sales?type=rtn',
          total: 'deposit/sales?type=rtn&total=1',
        }

      }
    ],
    search: '',
    loading: false,
    groupBy: 'user',
    dates: ["2020-12-01", "2020-12-31"],
    // dates: utility.currentDateRange,
    total:{
      items: [],
      headers: [
      
        {
          text: 'Deposits',
          value: 'count',
          class:['font-weight-bold', 'subtitle-1'],
          width: '120',
           component: {
                is: () => import('./../components/items/fields/Link.vue'),
                bind: {
                    path: '/deposit',
                    query: {
                        type: 'ftd',
                        'sortBy[]': ['created_on'],
                        created_on: vm => vm.dates.join('|'),
                        limit: (_, item) => item.count
                    }
                }
            } 
          
          
        },
        {
          text: 'Total',
          class:['font-weight-bold', 'subtitle-1'],
          value: 'amount',
          format: 'money'
        },
        {
          text: 'Performance',
          class:['font-weight-bold', 'subtitle-1'],
          value: 'deposits',
          width: 200,

          component: {
            is: () => import('./../components/items/fields/SparkLine.vue'),
            bind: {
              field: 'amount'
            }
          }
        },
        
        ]

    },
    table: {
      items: [],
      headers: [
      
        { 
            text: 'Owner', 
            sortable: false, 
            value: 'user.displayName', 
           
            component: () => import('./../components/items/fields/User.vue') 
        },
        { 
            text: 'Role', 
            sortable: false, 
            value: 'user.role', 
           
        },
        {
          text: 'Deposits',
          value: 'count',
          class:['font-weight-bold text-center'],
          width: '120',
           component: {
                is: () => import('./../components/items/fields/Link.vue'),
                bind: {
                    path: '/deposit',
                    query: {
                        user: (_, item) => item._id,
                        'sortBy[]': ['created_on'],
                        created_on: vm => vm.dates.join('|'),
                        limit: (_, item) => item.count
                    }
                }
            } 
        },
        {
          text: 'Total',
          value: 'amount',
          format: 'money'
        },
        { 
            text: 'T.A', 
            sortable: false, 
            value: 'user.target', 
          width: 150,
           
            component: {
              is: () => import('./../components/items/fields/Progress.vue'),
              bind: {
                field: 'amount'
              } 
            }
        },
        {
          text: 'Performance',
          value: 'deposits',
          width: 150,

          component: {
            is: () => import('./../components/items/fields/SparkLine.vue'),
            bind: {
              field: 'amount',
            }
          }
        },
       

      ]
    } 
     
  }),
  watch: {
    search: debounce( function () {
      this.fetch()
    }, 500),
    dates:{
      immediate:true,
      handler:'fetch' 
    },
    tab () {
      this.fetch()
      this.search = ''
    }
  },
  methods:{
    async fetch () {
    this.loading = true
    const query =  { 
      created_on: this.dates.join('|')
    } 
    if (this.search) {
      query['user.displayName'] = '~' + this.search.trim()
    }

    const response = await this.get({ collection: this.active.api.items, 
        query
      })
    const total = await this.get({ collection: this.active.api.total, 
      query: { 
        created_on: this.dates.join('|'),
      } 
    })
    this.total.items = total.data
    this.table.items = response.data  
    
    
    this.loading = false
      
    console.log(response);
    }
  } 
}
</script>

